body {
  color: #452b14;
  background-color: #dfd5d0;
  margin: 0;
  padding: 0;
  font-family: Ubuntu, sans-serif;
}

.container {
  background: #f2f1ef;
  border-radius: 60px;
  max-width: 960px;
  margin: 32px auto;
  padding: 32px;
}

h1, h2 {
  text-align: center;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin: 20px 0;
}

th, td {
  text-align: center;
  border: 1px solid #a68066;
  padding: 8px;
}

th {
  background-color: #dfd5d0;
}

button {
  color: #f2f1ef;
  cursor: pointer;
  background-color: #475800;
  border: none;
  border-radius: 40px;
  padding: 12px 16px;
}

.change {
  background-color: #475800;
  margin-bottom: 12px;
}

.delete {
  background-color: #475800;
}

form {
  margin-top: 20px;
}

form label {
  margin-bottom: 16px;
  display: block;
}

form input {
  box-sizing: border-box;
  border: none;
  border-radius: 12px;
  width: 100%;
  margin-top: 10px;
  padding: 12px;
}

.backdrop {
  z-index: 4;
  background: #0003;
  width: 100%;
  height: 100%;
  transition: opacity .4s cubic-bezier(.075, .82, .165, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.no-scroll {
  overflow: hidden;
}

.modal {
  z-index: 5;
  background-color: #f2f1ef;
  border-radius: 40px;
  width: calc(100% - 30px);
  max-width: 450px;
  padding: 40px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#close-btn {
  border-radius: 40px;
  position: absolute;
  top: 20px;
  right: 20px;
}
